.about {
  flex-direction: row;
  margin-top: 3em;
}

.about__name {
  color: var(--clr-primary);
}

.about__desc {
  font-size: 1rem;
  max-width: 400px;
}

.about .link--icon {
  margin-right: 0.8em;
}

.about .btn--outline {
  margin-right: 1em;
}

@media (max-width: 600px) {
  .about {
    flex-direction: column;
    margin-top: 3em;
  }
  .app .about {
    align-items: flex-start;
    margin-top: 2em;
  }
}
